import React from 'react';

export default class Experiencia extends React.Component{
    render(){
        return(
            <div style={{'position':'absolute','minHeight':'90%','minWidth':'100%','display':'flex','justifyContent':'center','flexDirection':'column'}} className="container">
                <div className='datos'>
                    <div>
                        <p><strong>Experiencia</strong></p>
                    </div>
                    <div style={{'borderTop':'solid', 'borderColor':'#17a2b8', 'width':'70%','paddingTop':'10px'}}>                        
                        <p><h5>Becario - Help Desk - Centro Internacional De Mejoramiento De Maiz Y Trigo (CIMMYT) Diciembre 2019 - Junio 2020</h5></p>                        
                        <div style={{'display': 'flex','justify-content': 'center'}}>
                        <ul>
                            <li>-Dar mantenimiento a equipos de computo</li>
                            <li>-Instalacion de equipo de computo y telefonia</li>
                            <li>-Instalacion de drivers para impresoras y scanners</li>
                            <li>-Atender tickets de los usuarios</li>
                            <li>-Resolucion de problemas con equipo de computo de los usuarios</li>
                            <li>-Resolucion de problemas de red</li>
                            <li>-Formateo de equipos de computo</li>
                        </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}