import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import MenuIcon from '@material-ui/icons/Menu';
import Informacion from './Informacion';
import Experiencia from './Experiencia';
import Educacion from './Educacion';
import Conocimientos from './Conocimientos';
import Proyectos from './Proyectos';


export default class Navegacion extends React.Component{

render(){

  return (
    <Router>

      <div>        
        <nav className="navbar navbar-expand-lg navbar-darck bg-dark">
          <button style={{'fontFamily':'poppins'}} class="btn btn-outline-info navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"><MenuIcon/>  </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent" style={{'justifyContent':'center'}}>
            <Link style={{'margin':'auto'}} to="/"><button id="boton" type="button" className="btn btn-outline-info">Informacion</button></Link>
            <Link style={{'margin':'auto'}} to="/ex"><button id="boton" type="button" className="btn btn-outline-info">Expereiencia</button></Link>
            <Link style={{'margin':'auto'}} to="/ed"><button id="boton" type="button" className="btn btn-outline-info">Educacion</button></Link>
            <Link style={{'margin':'auto'}} to="/co"><button id="boton" type="button" className="btn btn-outline-info">Conocimientos</button></Link>
            <Link style={{'margin':'auto'}} to="/pr"><button id="boton" type="button" className="btn btn-outline-info">Proyectos</button></Link> 
          </div>
        </nav>  
      </div>
        <Route path="/" exact component={Informacion} />
        <Route path="/ex"  component={Experiencia} />
        <Route path="/ed"  component={Educacion} />
        <Route path="/co"  component={Conocimientos} />
        <Route path="/pr"  component={Proyectos} />
      </Router>

  );
}

}
