import React from "react";

export default class Educacion extends React.Component {
  render() {
    return (
      <div
        style={{
          position: "absolute",
          minHeight: "90%",
          minWidth: "100%",
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
        }}
        className="container"
      >
        <div className="datos">
          <p>
            <strong>Educacion</strong>
          </p>
          <div style={{ display: "flex" }}>
            <div className="row">
              <div className="col-sm-6">
                <div style={{'backgroundColor':'black'}} className="card">
                  <div className="card-body">
                    <h5 className="card-title">Universidad Autonoma Del Estado De Mexico</h5>
                    <p className="card-text">
                    <img style={{'width':'50%'}} src={process.env.PUBLIC_URL+'/Images/escudoUAEM.png'} alt="UAEM"/>
                    </p>                    
                    <p>Pasante De La Carrera De Ingenieria En Computacion</p>
                  </div>
                </div>
              </div>
              <div className="col-sm-6">
                <div style={{'backgroundColor':'black'}} className="card">
                  <div className="card-body">
                    <h5 className="card-title">CBT Grabriel V. Alcocer Tepetlaoxtoc</h5>
                    <p className="card-text">
                    <img style={{'width':'40%'}} src={process.env.PUBLIC_URL+'/Images/escudoCBT.png'} alt="UAEM"/>
                    </p>         
                    <p>Titulo Como Tecnico En Informatica</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
