import React, { useState, useEffect } from "react";
import GitHubIcon from "@material-ui/icons/GitHub";
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CodeIcon from '@material-ui/icons/Code';
import ListIcon from '@material-ui/icons/List';




function Proyectos() {

  
  const [name, setName] = useState('');
  const [userName, setUserName] = useState('');
  const [repos, setRepos] = useState('');
  const [avatar, setAvatar] = useState('');

  useEffect(() => {
    fetch("https://api.github.com/users/LeonardoCR-Dev")
    .then(res=> res.json())
    .then(data => {
      setData(data)
    })
  }, []);

  const setData = ({
    name, 
    login,
    public_repos,
    avatar_url
  })=>{
    setName(name)
    setUserName(login)
    setRepos(public_repos)
    setAvatar(avatar_url)
  }
 
    return (
      
      <div id='proyectos'
        style={{
          position: "absolute",
          minHeight: "90%",
          minWidth: "100%",
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
        }}
        className="container"
      >
        <div className="datos">
          <div>
            <p>
              <strong>Proyectos</strong>
            </p>
          </div>
          <div
            style={{ borderTop: "solid", borderColor: "#17a2b8", width: "70%", paddingTop: "10px",}}>
            <div class="row">
              <div class="col-sm-6">
                <div style={{'backgroundColor':'black'}} class="card">
                  <div class="card-body">
                    <img className="foto" src={avatar} alt="" />
                  </div>
                </div>
              </div>
              <div class="col-sm-6">
                <div style={{'backgroundColor':'black'}} class="card">
                  <div style={{'textAlign':'left'}} class="card-body">
                    <p style={{'fontSize':'1.3em'}}><span style={{'marginRight':'15px'}}>Usuario:</span>{userName}</p>
                    <p style={{'fontSize':'1.3em'}}><span style={{'marginRight':'15px'}}>Nombre:</span>{name}</p>
                    <p style={{'fontSize':'1.3em'}}><span style={{'marginRight':'15px'}}>Repositorios:</span>{repos}</p>   
                    <Accordion style={{'backgroundColor':'black', 'color':'#17a2b8','fontFamily':'poppins','borderLeft':'solid'}}>
                      <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1a-content" id="panel1a-header">
                        <h3 style={{'marginRight':'15px'}}>Proyectos</h3> <CodeIcon fontSize='large'/>
                      </AccordionSummary>
                    <AccordionDetails>
                        <ul style={{'marginTop':'0px'}}>
                          <li>
                            <ListIcon fontSize='large'/> CV <code style={{'color':'#17a2b8'}}>Html - Css - Bootstrap</code>
                          </li>
                          <li>
                          <a href="https://github.com/LeonardoCR-Dev/CV/archive/page.zip">Descargar Archivo</a>
                          </li>
                          <li>
                            <ListIcon fontSize='large'/> Punto De Venta <code style={{'color':'#17a2b8'}}>Java</code>
                          </li>
                          <li>
                            <a href="https://github.com/LeonardoCR-Dev/Punto_V/archive/master.zip" download="CV.zip">Descargar Archivo</a>
                          </li>
                        </ul>
                    </AccordionDetails>
                  </Accordion>                 
                  </div>
                </div>
              </div>
            </div>
            <div style={{'paddingTop':'20px'}}>
            <a href="https://github.com/LeonardoCR-Dev?tab=repositories" target="_blank">
              <button type="button" className="btn btn-link">
                <GitHubIcon fontSize='large' />
                <span style={{'marginLeft':'15px','fontSize':'1.3em'}}>GitHub</span>
              </button>
            </a>
            </div>
          </div>
        </div>


            
      </div>
    );
  }

export default Proyectos;